:root {
  --col-dark: #000000;
  --col-light: #ffffff;
  --col-highlight: #FEBE10;
  --col-blue-300: #8EAFC4;
  --col-blue-400: #4f8cad;
  --col-grey-200: #fbfbfb;
  --col-grey-400: #767676;
  --transition: 0.3s ease;
  --fw-400: 300;
  --fw-500: 400;
  --fw-600: 500;
  --fw-900: 700;
  --h-top: 80px;
  --h-top-desktop: 130px;
  /* Responsive typography scaling */
  --fs-900: clamp(3rem, 5vw + 1rem, 4rem); /* h1, scales from 3rem on mobile to 4rem on larger screens */
  --fs-800: clamp(2.5rem, 4vw + 0.5rem, 3.2rem); /* h2, scales from 2.5rem to 3.2rem */
  --fs-700: clamp(2rem, 3vw + 0.5rem, 2.5rem); /* quotes, scales from 2rem to 2.5rem */
  --fs-600: clamp(1.75rem, 2vw + 0.5rem, 2rem); /* h3, scales from 1.75rem to 2rem */
  --fs-550: clamp(1.5rem, 1.5vw + 0.3rem, 1.8rem); /* larger text, scales from 1.5rem to 1.8rem */
  --fs-500: clamp(1.25rem, 1vw + 0.25rem, 1.5rem); /* large text, scales from 1.25rem to 1.5rem */
  --fs-400: 1rem; /* body text remains 1rem */
  --fs-300: 0.778rem; /* buttons fixed at 14px (optional to keep this fixed) */
  --ff-body: "NeueHaasDisplay", times, "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

