/*-------------------- contact --*/
@keyframes loop {
  0% {
    rotate: 0deg;
  }
  100% {
    rotate: 360deg;
  }
}
.circle-path,
.link-arrow-path {
  display: none;
}

.link {
  display: inline-block;
  width: 120px;
  height: 120px;
  transform-origin: center;
  position: relative;
}
.link:hover .circle-anim {
  scale: 0.85;
}
.link .link-arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  stroke-width: 2px;
  stroke: var(--col-blue-400);
  fill: none;
}
.link .container-link-circle {
  width: 100%;
  height: 100%;
  display: block;
}
.link .circle-anim {
  width: 100%;
  height: 100%;
  display: block;
  transition: all 500ms cubic-bezier(0.245, 0, 0, 1);
}
.link .link-circle {
  width: 100%;
  height: 100%;
  animation-name: loop;
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.link .link-circle text {
  font-size: 13px;
  line-height: 21px;
  letter-spacing: 0.675px;
  fill: var(--col-dark);
  font-family: var(--ff-body);
}

header {
  background-color: hsl(0deg, 0%, 100%);
  backdrop-filter: blur(4px);
}
@media (min-width: 768px) {
  header {
    background-color: hsla(0deg, 0%, 100%, 0.8);
  }
}
header #main-menu {
  column-gap: 1rem;
}
header .nav-logo {
  width: 70px;
  height: 70px;
  transition: all 0.3s ease-out;
}
@media (min-width: 320px) {
  header .nav-logo {
    width: 40px;
    height: 40px;
  }
}
@media (min-width: 480px) {
  header .nav-logo {
    width: 70px;
    height: 70px;
  }
}
header .eyes {
  animation: eyeflash 12s infinite;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
}
@keyframes eyeflash {
  0% {
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  96% {
    opacity: 0;
  }
  97% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
header .current_page_parent a,
header .current-menu-item a {
  font-weight: var(--fw-600);
}
@media (min-width: 768px) {
  header .current_page_parent a::before,
  header .current-menu-item a::before {
    width: 100%;
    background: var(--col-highlight);
    transition: background var(--transition);
  }
}
header .nav-link {
  font-size: 1.2em;
  font-weight: 400;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  position: relative;
  color: #000;
}
@media (min-width: 768px) {
  header .nav-link {
    padding: 1.5rem 1rem;
    line-height: 12px;
  }
}
header .nav-link::before {
  content: "";
  position: absolute;
  width: 0;
  height: 15px;
  display: block;
  bottom: 1rem;
  left: 0;
  background: var(--col-blue-400);
  transition: width 0.3s ease;
  z-index: -10;
}
header .nav-link[aria-current=page],
header .nav-link .current-menu-item {
  font-weight: var(--fw-600);
}
@media (min-width: 768px) {
  header .nav-link:hover::before {
    width: 100%;
    left: 0;
    background: var(--col-blue-400);
    z-index: -10;
  }
  header .nav-link[aria-current=page]::before {
    width: 100%;
    background: var(--col-highlight);
  }
}
header .current-menu-parent > a {
  font-weight: var(--fw-600);
}
@media (min-width: 768px) {
  header .current-menu-parent > a::before {
    width: 100%;
    background: var(--col-highlight);
    transition: background var(--transition);
  }
}
header .dropdown-item:active {
  background-color: var(--col-blue-400);
}
header .dropdown-item[aria-current=page] {
  font-weight: var(--fw-600);
}
header.hidden .nav-logo {
  width: 50px;
  height: 50px;
}

footer {
  background-color: var(--col-blue-400);
}
footer .menu {
  list-style: none;
}
footer a {
  text-decoration: none;
  color: var(--col-light);
  transition: color var(--transition);
}
footer a:hover {
  color: var(--col-highlight);
}
footer .footer__logo {
  width: min(240px, 80vw);
}
footer .colophon {
  font-size: var(--fs-300);
  color: var(--col-light);
}
footer .colophon a {
  text-decoration: none;
  color: inherit;
  transition: color var(--transition);
}
footer .colophon a:hover {
  color: var(--col-highlight);
}
footer li {
  margin-bottom: 0.25em;
}

.footer__contact {
  padding-inline-start: 0;
}
.footer__contact li {
  list-style-type: none;
  padding-left: 25px;
  position: relative;
}

