a.icon {
  position: relative;
  display: inline-block;
  width: 2em;
  height: 2em;
  margin-right: 0.5em;
}

.blog {
  margin-top: 100px;
}
.blog h1.blog__title, .blog .blog__title.h1 {
  margin-bottom: 0.5rem;
  font-size: var(--fs-700);
}
.blog .reading {
  font-size: var(--fs-300);
}
.blog h2, .blog .h2 {
  margin-top: 1em;
  margin-bottom: 0.5rem;
  font-size: var(--fs-600);
}
.blog h3, .blog .h3 {
  margin-top: 1em;
  margin-bottom: 0.5rem;
  font-size: var(--fs-500);
}
.blog .sidebar {
  position: sticky;
  top: 5rem;
}
.blog .quick_links {
  background-color: var(--col-grey-200);
  padding: 1rem 2rem;
  margin-bottom: 1rem;
}
.blog .quick_links a {
  font-weight: var(--fw-400);
  text-decoration: none;
}
.blog .quick_links ul {
  list-style: none;
}
.blog .quick_links li {
  margin-bottom: 0.5rem;
}
.blog__content a {
  font-weight: var(--fw-600);
}

.news_index__grid {
  display: grid;
  gap: 1.5rem;
}
@media (min-width: 992px) {
  .news_index__grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .news_index__grid .cta {
    grid-column: span 2;
  }
}
.news_index__card {
  display: grid;
  gap: 1rem;
  grid-auto-rows: max-content;
  border-bottom: 1px solid var(--col-primary-900);
  font-weight: var(--fw-400);
  background-color: var(--col-grey-200);
  padding: 1rem;
  box-shadow: var(--shadow-in);
  transition: transform var(--transition), box-shadow var(--transition);
}
.news_index__card:hover {
  box-shadow: var(--shadow-in-2);
  transform: translateY(2px);
}
.news_index__card img {
  width: 100%;
  aspect-ratio: 16/9;
  object-fit: cover;
  object-position: center;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5019607843);
}
@media (min-width: 992px) {
  .news_index__card--first {
    grid-column: 1/-1;
    grid-template-columns: 0.75fr 1fr;
  }
  .news_index__card--first .news_index__inner {
    grid-template-rows: repeat(3, auto);
  }
  .news_index__card--first .news_index__inner h2, .news_index__card--first .news_index__inner .h2 {
    font-size: var(--fs-700);
  }
  .news_index__card--first .news_index__inner p {
    font-size: var(--fs-400);
  }
}
.news_index__image {
  position: relative;
  width: 100%;
  aspect-ratio: 16/9;
}
.news_index__image img {
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.news_index__inner {
  display: grid;
  grid-template-rows: minmax(4rem, auto) auto;
  color: var(--col-dark);
}
.news_index__inner h2, .news_index__inner .h2 {
  margin-top: 0;
  font-size: var(--fs-500);
  font-family: var(--ff-body);
  font-weight: var(--fw-900);
}
.news_index__inner p {
  font-size: var(--fs-300);
}
.news_index__meta {
  display: flex;
  gap: 0.5rem;
  justify-content: space-between;
  font-size: var(--fs-300);
}
.news_index__meta.news_index__meta--first {
  justify-content: start;
  align-items: flex-end;
}
.news_index__category {
  background-color: var(--col-blue-400);
  color: var(--col-light);
  font-family: var(--ff-body);
  font-size: var(--fs-300);
  font-weight: var(--fw-600);
  padding-inline: 0.5rem;
  padding-block: 0.25 rem 0;
}

.sticky-offset {
  top: var(--h-top);
}

.work_item {
  margin-top: calc(var(--h-top) + 2rem);
}
.work_item blockquote {
  font-size: var(--fs-600);
  font-weight: var(--fw-600);
  line-height: 1.2;
}
.work_item .work_item {
  display: block;
  isolation: isolate;
  overflow: hidden;
  margin-bottom: 1rem;
  position: relative;
}
.work_item .work_item::after {
  content: "";
  position: absolute;
